<template>
  <div>
    <el-card>
      <div slot="header">基本信息
        <el-button style="float: right" type="info" size="small" @click="$router.go(-1)">返回</el-button>
      </div>
      <div class="tablediv">

          <table border="1">
            <tbody>

                <tr>
                    <td width="105" class="key">负责人</td>
                    <td width="280" >
                        {{$route.query.name}}
                    </td>   
                  
                
                    <td width="105" class="key">著作名称</td>
                    <td width="280" >
                        {{Form.wrName}}
                    </td>
                 </tr>
                <tr>
                    <td v-if="this.istea == 1" width="105" class="key">所属单位</td>
                    <td v-else width="105" class="key">所属学院</td>
                    <td width="280" >
                        {{Form.belongDept}}
                    </td>
                  
                
                    <td width="105" class="key">出版单位</td>
                    <td width="280" >
                        {{Form.wrPublishUnit}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">出版时间</td>
                    <td width="280" >
                        {{Form.wrPublishTime}}
                    </td>
                  
                
                    <td width="105" class="key">出版地（国内、国外）</td>
                    <td width="280" >
                        {{Form.wrPublishPlace}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">著作类别（工具书、专著、编著、译著、教材、皮书</td>
                    <td width="280" >
                        {{Form.wrType}}
                    </td>
                  
                
                    <td width="105" class="key">出版社级别（一级、二级、三级、其他）</td>
                    <td width="280">
                        {{Form.publisherLevel}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">学科门类（科技类、社科类）</td>
                    <td width="280" >
                        {{Form.subjectCategory}}
                    </td>
                  
                
                    <td width="105" class="key">一级学科</td>
                    <td width="280">
                        {{Form.firstLevelDiscipline}}
                    </td>
                 </tr>  

                 <tr>
                    <td width="105" class="key">项目来源</td>
                    <td width="280" >
                        {{Form.projectSource}}
                    </td>
                  
                
                    <td width="105" class="key">字数：（ ）单位：万字</td>
                    <td width="280">
                        {{Form.wordCount}}
                    </td>
                 </tr>  

                 <tr>
                    <td width="105" class="key">是否译成外文</td>
                    <td width="280" >
                        {{Form.isTranslation}}
                    </td>
                  
                
                    <td width="105" class="key">学校署名（第一单位、非第一单位）</td>
                    <td width="280">
                        {{Form.schoolSignature}}
                    </td>
                 </tr>  

                 <tr>
                    <td width="105" class="key">语种</td>
                    <td width="280" >
                        {{Form.wrLanguage}}
                    </td>
                  
                
                    <td width="105" class="key">ISSN号</td>
                    <td width="280">
                        {{Form.issnNumber}}
                    </td>
                 </tr>  

                 <tr>
                    <td width="105" class="key">CIP号</td>
                    <td width="280">
                        {{Form.cipNumber}}
                    </td>
                  
                
                    
                    <td width="105" class="key">简介</td>
                    <td width="280">
                        {{Form.wrIntro}}
                    </td>
                 </tr>  
            </tbody>
        </table>
      </div>
    </el-card>

    <el-card>
      <div slot="header">作者信息</div>
      <div>
        <el-table
          :data="authForm"
          border
          stripe
        >
          <el-table-column
          prop="authorType"
          label="作者类型"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorName"
          label="作者姓名"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorId"
          label="作者编号"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="authorSex"
          label="性别"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="signingOrder"
          label="署名顺序"
          align="center"
        ></el-table-column>

        <el-table-column 
        prop="authorRole" 
        label="承担角色" 
        align="center">
        </el-table-column>

        <el-table-column
          prop="authorEducation"
          label="学历"
          align="center"
        ></el-table-column>

        <el-table-column
        v-if="this.istea == 1"
          prop="authorTitle"
          label="职称"
          align="center"
        ></el-table-column>

        <el-table-column
        v-if="this.istea == 1"
          prop="authorWorkUnit  "
          label="工作单位"
          align="center"
        ></el-table-column>
        

        <el-table-column
          prop="contributionRate"
          label="贡献率：（ ）单位：%"
          align="center"
        ></el-table-column>

        </el-table>
      </div>
    </el-card>

      <el-dialog
          :visible.sync="dialogVisible">
          <img width="100%" :src="photoView" >
        </el-dialog>

    <el-card>
      <div slot="header">附件信息</div>
      <div>
        <el-table :data="accessory" border stripe>

          <el-table-column
            label="附件"
            align="center"
          >
            <template slot-scope="scope">
              <img @click="photo(scope.row)" :src="scope.row.url" alt=""  style="width: 80px;height: 80px">
            </template>
          </el-table-column>

        

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <el-tooltip
                class="item"
                effect="dark"
                content="预览"
                placement="top"
                :enterable="false"
              >
                  <el-button
                    @click="photo(scope.row)"
                    class="view"
                    type="primary"
                    icon="el-icon-view"
                    size="mini"
                  ></el-button>
              </el-tooltip>

              <el-tooltip
                class="item"
                effect="dark"
                content="下载"
                placement="top-start"
                :enterable="false"
              >
                <a :href="scope.row.url" download="附件">
                  <el-button
                    class="download"
                    type="primary"
                    icon="el-icon-download"
                    size="mini"
                  ></el-button>
                </a>
                
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      photoView:'',
      dialogVisible: false,
      authForm:{},
      accessory:[],
      Options:[],
      Form: {},
      authForm:[],
      List: [],
      istea : undefined,
    }
  },
  created() {
    this.isAuthority()
      this.getDetail()
      this.getTreeselect()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    photo(row){
      this.dialogVisible = true
      this.photoView = row.url
      // console.log(this.photoView);
    },
    async getTreeselect() {
      this.istea = this.$route.query.istea
      const id = this.$route.query.id
      // console.log(istea);
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/copyRightInfo/queryTeaCopyRightInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }else if(this.istea == 0){
        const { data: res } = await this.$http.post(
          '/copyRightInfo/queryStuCopyRightInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g,'teaId'))
        this.Options = JSON.parse(JSON.stringify(a).replace(/stuName/g,'teaName'))
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }

    },

    async getDetail() {
      if (!this.$route.query.id.length) {
        return
      }
      const id = this.$route.query.id
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/worksResultsInfo/getByIdWorksResultsInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
      this.getAccessory()
    },

    async getAutherinfo(){
      const { data: res } = await this.$http.get(
        '/worksResultsAuthorInfo/queryWorksResultsAuthorInfoList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm);
    },

    async getAccessory() {
      const { data: res } = await this.$http.get(
        '/accessoriesList/getByIdAccessoriesList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.accessory = res.data.entity
      console.log(this.accessory)
    },
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-bottom: 20px;
}
.save{
  float: right;
  margin-right: 10px;
}
table{
    border: 1px solid #d3d3d3;
    border-collapse: collapse;
    width: 100%;
    color: #606266;
    line-height: 45px;
    td{
        padding-left: 22px;
    }
}
.key{
    color: #909399;
    background-color: #f9f9f9;
}
.view{
  margin-right: 10px;
}
</style>
